import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {ActivatedRoute} from '@angular/router';
import { HttpParams } from '@angular/common/http';
@Component({
  selector: 'app-advice',
  templateUrl: './advice.component.html',
  styleUrls: ['./advice.component.css']
})
export class AdviceComponent implements OnInit {

  constructor(private http:HttpClient,private routerinfo:ActivatedRoute,) { }

id:any;
  userid:any;
  ngOnInit() {
    this.id=this.routerinfo.snapshot.queryParams["id"]
    this.getuser();
  }
  arr;
  getuser(){
    this.http.get('http://www.zhuoran.fun:3000/advicesel').subscribe(res=>{
      this.arr= res;
    },err=>{
      console.log("ERROR:",err)
    })
  }
  
  private encodeHttpParams(params: any): any {
    if (!params) return null;
    return new HttpParams({fromObject: params});
  }

  content;

  send(adv_id){
    
    var params = {
      reply:this.content,
      adv_id : adv_id
     }
     console.log(this.content);
     if(this.content != null){
       this.http.post('http://www.zhuoran.fun:3000/reply',this.encodeHttpParams(params)).subscribe(res=>{
      alert('回复成功');
      this.getuser();
      this.content = '';
      console.log(res)
    },err=>{
      alert('回复失败')
      console.log(err)
    })
     }else{
       alert('回复不能为空');
     }
    
  }

 


}

