import { Component, OnInit,AfterViewChecked } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-data',
  templateUrl: './data.component.html',
  styleUrls: ['./data.component.css']
})
export class DataComponent implements OnInit {

  constructor(private http:HttpClient) { }
  arr1;
  header;
  people;
  srore;
  mark;
  time=new Date().getFullYear()+'-'+new Date().getMonth()+'-'+new Date().getDate()+' '+new Date().getHours()+":"+new Date().getMinutes()+":"+new Date().getSeconds();
  // isActive=0;
  isActive:string='0';
  ngOnInit() {
    this.getDataZ();
    $(function($){ 
      $('#ha').click(function(){
        $('#fileUpload').val('');
      })
    });
  }
  which(i){
    this.isActive=i;
    this.http.get("http://www.zhuoran.fun:3000"+"/learnFile?fileVerify="+i).subscribe(res=>{
      console.log(res);
     this.arr1=res;
      },err=>{
        console.log('error',err)
      })
  }
getDataZ(){
  this.http.get("http://www.zhuoran.fun:3000"+"/learnFile?fileVerify="+this.isActive).subscribe(res=>{
    console.log(res);
   this.arr1=res;
    },err=>{
      console.log('error',err)
    })
}
id;
//删除---------------------------------
del(idx){
  this.id=idx;
}
del1(){
  this.http.get('http://www.zhuoran.fun:3000/delLearnFile'+"?id="+this.id).subscribe(res=>{
    console.log(res['message']);
  },err=>{
    console.log("ERROR:",err)
  })
  this.getDataZ();
}
upload(){
 
  var formData = new FormData(); 
  var file = document.getElementById("fileUpload")['files'][0];
  console.log(this.people);
  console.log(this.srore);
  console.log(this.header);
  console.log(file);
  if((this.header!==undefined)&& (this.people !==undefined)&&(this.srore!==undefined)&&(file!==undefined)){
       formData.append("file",file); 
    formData.append('file_name',this.header);
    formData.append('upload_peo',this.people);
    formData.append('upload_time',this.time);
    formData.append('fileVerify',this.isActive);
    formData.append('srore',this.srore);
   $.ajax({
      url:'http://www.zhuoran.fun:3000/upLearnFile',
      type: 'POST',
      data:formData,
      async: false,
      cache: false,
      contentType: false,
      processData: false,
      success:function (res) {
        console.log("成功");
    }
  })
  this.header='';
  this.people='';
  this.srore='';
  this.getDataZ();
  }
  
  

}
}
