import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {ActivatedRoute} from '@angular/router';
@Component({
  selector: 'app-tea',
  templateUrl: './tea.component.html',
  styleUrls: ['./tea.component.css']
})
export class TeaComponent implements OnInit {

  constructor(private http:HttpClient,private routerinfo:ActivatedRoute,) { }
  
  id:any;
  ngOnInit() {
    this.id=this.routerinfo.snapshot.queryParams["id"]
    this.getuser();
  }
  arr=[]
  getuser(){
    this.http.get('http://www.zhuoran.fun:3000/select_tea').subscribe(res=>{
      this.arr[0] = res[this.id];
      console.log(this.arr)
    },err=>{
      console.log("ERROR:",err)
    })
  }

}
