import { Component, OnInit} from '@angular/core';
import { HttpClient } from '@angular/common/http';
import * as $ from 'jquery';
@Component({
  selector: 'app-video',
  templateUrl: './video.component.html',
  styleUrls: ['./video.component.css']
})
export class VideoComponent implements OnInit{

  constructor(private http:HttpClient) { }
arr;
header;
content;
src;
time=new Date().getFullYear()+'-'+new Date().getMonth()+'-'+new Date().getDate()+' '+new Date().getHours()+":"+new Date().getMinutes()+":"+new Date().getSeconds();
  ngOnInit() {
    this.getVideo();
    $(function($){ 
      $('#ha').click(function(){
        $('#fileUpload').val('');
      })
    });
  }
 getVideo(){
  this.http.get('http://www.zhuoran.fun:3000/video').subscribe(res=>{
    console.log(res)
    this.arr=res;
  },err=>{
    console.log("ERROR:",err)
  })
 }
 id;
//删除---------------------------------
del(idx){
  this.id=idx;
}
del2(){
  this.http.get('http://www.zhuoran.fun:3000/deVideo'+"?id="+this.id).subscribe(res=>{
    console.log(res['message']);
  },err=>{
    console.log("ERROR:",err)
  })
  this.getVideo();
}

//发布----------------------------------------------------------------------------
upload(){
  var formData = new FormData(); 
  var file = document.getElementById("fileUpload")['files'][0];
  if((this.header!==undefined)&&(this.content!==undefined)&&(file!==undefined)){
    formData.append("file",file); 
    formData.append('course_name',this.header);
    formData.append('course_author',this.content);
    formData.append('course_time',this.time);
     $.ajax({
    url:'http://www.zhuoran.fun:3000/createVideo',
    type: 'POST',
    data:formData,
    async: false,
    cache: false,
    contentType: false,
    processData: false,
    success:function (res) {
      console.log("成功");
  
    }
  })
  this.getVideo();
  this.header='';
  this.content='';
  }
 
}
}
