import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {ActivatedRoute} from '@angular/router';
@Component({
  selector: 'app-dd',
  templateUrl: './dd.component.html',
  styleUrls: ['./dd.component.css']
})
export class DdComponent implements OnInit {

  constructor(private http:HttpClient,private routerinfo:ActivatedRoute,) { }
  id:any;
  ngOnInit() {
    this.id=this.routerinfo.snapshot.queryParams["id"]
    this.getuser();
  }
  arr=[]
  getuser(){
    this.http.get('http://www.zhuoran.fun:3000/showOrders').subscribe(res=>{
      console.log(res)
      this.arr[0] = res[this.id];
      
    },err=>{
      console.log("ERROR:",err)
    })
  }
}
